"use client";

import RevenueBadge from "@/components/RevenueBadge";
import Image from "next/image";
import { useRouter } from "next/navigation";
import { use, useEffect, useState } from "react";

export default function ListingDetailPage({ 
  params 
}: { 
  params: Promise<{ id: string }> 
}) {
  const router = useRouter();
  const resolvedParams = use(params); // Next.js 15+ Î∞©Ïãù
  
  const [listing, setListing] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  // Îç∞Ïù¥ÌÑ∞ Î°úÎìú
  useEffect(() => {
    async function fetchDetail() {
      try {
        setLoading(true);
        // API Í≤ΩÎ°úÎ•º /api/listings/[id] Î°ú Ìò∏Ï∂ú
        const res = await fetch(`/api/listings/${resolvedParams.id}`, {
          cache: 'no-store'
        });
        
        if (!res.ok) throw new Error("Not Found");
        
        const data = await res.json();
        setListing(data);
      } catch (e) {
        console.error("Îç∞Ïù¥ÌÑ∞ Î°úÎìú Ïã§Ìå®:", e);
        setError(true);
      } finally {
        setLoading(false);
      }
    }
    if (resolvedParams.id) fetchDetail();
  }, [resolvedParams.id]);

  // Ï±ÑÌåÖÎ∞© ÏÉùÏÑ± Ìï®Ïàò
  const startChat = async (type: string) => {
    if (!listing) return;
    try {
      const res = await fetch("/api/chat/threads", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          type,
          listingId: listing.id,
          title: listing.title,
        }),
      });

      // ÏÑúÎ≤ÑÏóêÏÑú 401(Unauthorized)ÏùÑ Î≥¥ÎÇ∏ Í≤ΩÏö∞
      if (res.status === 401) {
        alert("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú ÏÑúÎπÑÏä§ÏûÖÎãàÎã§.");
        router.push(`/login?callbackUrl=/listing/${listing.id}`);
        return;
      }

      const data = await res.json();
      if (data.thread?.id) {
        // ÏÑ±Í≥µ Ïãú Ï±ÑÌåÖÎ∞©ÏúºÎ°ú Ïù¥Îèô
        router.push(`/chat/${data.thread.id}?role=user`);
      }
    } catch (e) {
      console.error(e);
      alert("Ïó∞Í≤∞ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    }
  };

  if (loading) return <div className="p-20 text-center text-gray-500 font-medium">Îß§Î¨º Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ë...</div>;
  
  if (error || !listing) return (
    <div className="p-20 text-center">
      <h2 className="text-xl font-bold mb-2">Îß§Î¨ºÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.</h2>
      <button onClick={() => router.push('/listing')} className="bg-blue-600 text-white px-6 py-2 rounded-lg mt-4">Î™©Î°ùÏúºÎ°ú Ïù¥Îèô</button>
    </div>
  );

  const photos = listing.photos ? (typeof listing.photos === 'string' ? JSON.parse(listing.photos) : listing.photos) : [];
  const fmt = (n: number | null) => n?.toLocaleString() ?? "-";

  return (
    <div className="min-h-screen bg-gray-50 pb-20">
      <div className="container mx-auto px-4 py-8">
        {/* 1. Ïù¥ÎØ∏ÏßÄ Í∞§Îü¨Î¶¨ */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 h-[400px] mb-8 overflow-hidden rounded-2xl shadow-md bg-white">
          <div className="relative h-full bg-gray-100">
            {photos[0] ? (
              <Image src={photos[0]} alt="Î©îÏù∏" fill className="object-cover" />
            ) : <div className="grid place-items-center h-full text-gray-400">Ïù¥ÎØ∏ÏßÄ ÏóÜÏùå</div>}
          </div>
          <div className="hidden md:grid grid-cols-2 grid-rows-2 gap-4 h-full">
            {[1, 2, 3, 4].map((i) => (
              <div key={i} className="relative bg-gray-100 border-l border-white">
                {photos[i] && <Image src={photos[i]} alt="ÏÑúÎ∏å" fill className="object-cover" />}
              </div>
            ))}
          </div>
        </div>

        {/* 2. Î©îÏù∏ Ï†ïÎ≥¥ Ïª®ÌÖêÏ∏† */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          <div className="lg:col-span-2 space-y-8">
            <div className="bg-white p-8 rounded-2xl border border-gray-100 shadow-sm">
              {listing.isRevenueVerified && <div className="mb-2"><RevenueBadge /></div>}
              <h1 className="text-3xl font-bold text-gray-900">{listing.title}</h1>
              <p className="text-gray-500 mt-2 text-lg">{listing.address_area} ¬∑ {listing.sector}</p>
              
              <div className="grid grid-cols-3 gap-4 mt-8 pt-8 border-t border-gray-100 text-center">
                <div>
                  <p className="text-gray-400 text-sm">Í∂åÎ¶¨Í∏à</p>
                  <p className="text-2xl font-bold text-blue-600">{fmt(listing.goodwill_price)}Îßå</p>
                </div>
                <div>
                  <p className="text-gray-400 text-sm">Î≥¥Ï¶ùÍ∏à/ÏõîÏÑ∏</p>
                  <p className="text-2xl font-bold text-gray-800">{fmt(listing.deposit)}/{fmt(listing.rent_monthly)}</p>
                </div>
                <div>
                  <p className="text-gray-400 text-sm">Î©¥Ï†Å</p>
                  <p className="text-2xl font-bold text-gray-800">{listing.area_pyeong || 0}Ìèâ</p>
                </div>
              </div>
            </div>

            <div className="bg-white p-8 rounded-2xl border border-gray-100 shadow-sm">
              <h2 className="text-xl font-bold mb-4">ÏÉÅÏÑ∏ ÏÑ§Î™Ö</h2>
              <p className="text-gray-700 leading-relaxed whitespace-pre-wrap">{listing.description || "ÏÉÅÏÑ∏ ÏÑ§Î™ÖÏù¥ ÏóÜÏäµÎãàÎã§."}</p>
            </div>
          </div>

          {/* 3. Ïö∞Ï∏° Í≥†Ï†ï Î¨∏ÏùòÎ∞î */}
          <div className="space-y-4">
            <div className="bg-white p-6 rounded-2xl border border-gray-200 sticky top-24 shadow-lg ring-1 ring-black/5">
              <h3 className="text-lg font-bold mb-4">Ï†ÑÎ¨∏Í∞Ä ÏÉÅÎã¥</h3>
              <div className="space-y-3">
                <button 
                  onClick={() => startChat("listing")}
                  className="w-full bg-blue-600 text-white py-4 rounded-xl font-bold text-lg hover:bg-blue-700 transition-all active:scale-95 shadow-md shadow-blue-100"
                >
                  üí¨ Ïã§ÏãúÍ∞Ñ Ï±ÑÌåÖ Î¨∏Ïùò
                </button>
                <div className="grid grid-cols-2 gap-2">
                  <button onClick={() => startChat("loan")} className="bg-gray-50 py-3 rounded-xl font-medium text-gray-700 hover:bg-gray-100 border border-gray-200">üí∞ ÎåÄÏ∂ú ÏÉÅÎã¥</button>
                  <button onClick={() => startChat("fitout")} className="bg-gray-50 py-3 rounded-xl font-medium text-gray-700 hover:bg-gray-100 border border-gray-200">üî® Ïù∏ÌÖåÎ¶¨Ïñ¥</button>
                </div>
                <p className="text-center text-[10px] text-gray-400 mt-6 tracking-widest uppercase">Item No. {listing.id}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}